						function move() {
							if (div){
								if(div.scrollTop==scrollTop){
									div.scrollTo({
										top: 0, 
										behavior: "smooth"
									});
									return false
								}
								scrollTop = div.scrollTop
								div.scrollTo({ 
									top: div.scrollTop + 40, 
									behavior: "smooth"
								});
							}
								
						}
						this.monthlysalesofcarmodelsTimer = setInterval(move, 2000);
						div.addEventListener('mouseenter', e => {
							e.stopPropagation()
							clearInterval(this.monthlysalesofcarmodelsTimer);
						})
						div.addEventListener('mouseleave', e => {
							e.stopPropagation()
							this.monthlysalesofcarmodelsTimer = setInterval(move, 2000);
